<script>
	export default {
		onLaunch: function() {
			console.log('App Launch')
			// 判断token是否过期
			let userInfo = uni.getStorageSync('userInfo');
			console.log(userInfo)
			if (userInfo) {
				let expTime = userInfo.expTime;
				let nowTime = parseInt(new Date().getTime());
				if (expTime <= nowTime) {
					uni.reLaunch({
						url: '/pages/login'
					})
					uni.showToast({
						title: '登录失效，请重新登陆',
						icon: 'none'
					})
				}
			}

			uni.getStorage({
				key: 'token',
				success: res => {
					// console.log(res);
				},
				fail: err => {
					uni.reLaunch({
						url: '/pages/login'
					})
				}
			})
			let phoneStatus 
			// 获取手机型号
			uni.getSystemInfo({
				success:res=>{
					console.log(res)
					phoneStatus = res.platform
				}
			})
			const jyJPush = uni.requireNativePlugin('JY-JPush');
			if (jyJPush) {
				// if(userInfo){
				jyJPush.setJYJPushAlias({
					//  按照自己的业务需求来设置
					userAlias: '1'
				}, result => {
					//  设置成功或者失败，都会通过这个result回调返回数据；数据格式保持极光返回的安卓/iOS数据一致
					//  注：若没有返回任何数据，考虑是否初始化完成
					console.log(result)
					// uni.showModal({
					// 	title: '设置别名',
					// 	content:JSON.stringify(result)
					// })
				});
				// addJYJPushReceiveBackgroudNotificationListener
				jyJPush.addJYJPushReceiveNotificationListener(result => {
					//  监听成功后，若收到推送，会在result返回对应的数据；数据格式保持极光返回的安卓/iOS数据一致
					console.log(result)
					uni.showModal({
						title: result.notificationTitle,
						content: result.notificationContent,
						showCancel:false  
					})
				});
				// 
				jyJPush.addJYJPushReceiveOpenNotificationListener(res => {
					console.log(res);
				})
				if(phoneStatus=='android'){
					// 安卓检查是否开启通知权限
					jyJPush.android_isNotificationEnabled(result => {
						/*
						status = 0，关闭
						status = 1, 开启
						status = -1, 检测失败
						*/
						console.log(result);
						if(result.status == 0){						
							uni.showModal({
								title:'推送设置',
								content:'当前检测未打开通知权限,是否前往开启？',
								success: (res) => {
									if(res.confirm){									
										// 安卓打开通知权限
										jyJPush.android_goToAppNotificationSettings(result => {
											console.log(JSON.stringify(result), 打开通知权限);
										});						
									}
								}
							})
						}
					});					
				}
				if(phoneStatus=='ios'){
					// ios检测打开权限
					jyJPush.ios_requestNotificationAuthorization(result=> {
						/*
						status = 0，用户尚未对该应用的权限做出选择
						status = 1, 被关闭了权限
						status = 2, 开启了权限
						status = 3, 开启了非中断用户通知权限，iOS12支持
						*/
						console.log(JSON.stringify(result));
						if(result.status==0||result.status==1){
							uni.showModal({
								title:'推送设置',
								content:'当前检测未打开通知权限,是否前往开启？',
								success:res=>{
									if(res.confirm){
										jyJPush.ios_openSettingsForNotification(result=> {
											console.log(JSON.stringify(result));
										});										
									}
								}
							})
						}
					});																		
				}
			}
		},
		
		onShow: function() {
			console.log('App Show')

		},
		
		onHide: function() {
			console.log('App Hide')
		}
	}
</script>

